.hamburger {
    /* background-color: rgb(208, 130, 94) !important; */
    border: none;
    z-index: 2;
    padding: 0;
    background-color:rgb(208, 130, 94);
    -webkit-animation: background-color-pulsate 10s infinite;
            animation: background-color-pulsate 10s infinite;
}


.hamburger-text {
    color: white !important;
    font-size: 2rem;
}

.hamburger-icon {
    height: 35px;
    width: 35px;
    
}

@-webkit-keyframes background-color-pulsate {
    0%   { background-color: rgb(208, 130, 94); }
    50% { background-color: rgb(255, 255, 255); }
    100%   { background-color: rgb(208, 130, 94); }
}

@keyframes background-color-pulsate {
    0%   { background-color: rgb(208, 130, 94); }
    50% { background-color: rgb(255, 255, 255); }
    100%   { background-color: rgb(208, 130, 94); }
}


.hamburger, .hamburger-container
.collapsed {
	display: flex;
	justify-content: center;
	align-items: center;
    position: absolute;
	top: 1em;
	right: 1em;
	border-radius: 50px;
	height: 60px;
	width: 60px;
    box-shadow: 10px 10px 10px rgba(32, 32, 32, 0.986);
}

.hamburger-text {
	color: black;
	text-shadow: 2px 2px #8a8a8a;
}

.menu {
    position: absolute;
    background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
    background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
    z-index: 1;
    width: 78%;
    height: 100vh;
    overflow: auto;
    padding: 10px;
    border-radius: 35px 35px;
    box-shadow: 10px 10px 10px rgba(32, 32, 32, 0.986);
}

@media only screen and (min-width: 424px){
    .hamburger-container, .hamburger {
        display: none;
    }
}
:root {
	background-color: black;
}

.app {
	height: 100vh;
	display: flex;
	color: white;
	font-family: 'Montserrat', sans-serif;
}
  
.app-left {
	width: 50%;
	/* background-color: rgb(208, 130, 94); */
	background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
	background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
	overflow: auto;
    box-shadow: 10px 10px 10px rgba(32, 32, 32, 0.986);
}
  
.app-right {
	width: 50%;
	/* background-color: #2E4158; */
	background: -webkit-gradient(linear, left top, left bottom, from(#2E4158), to(#111111));
	background: linear-gradient(#2E4158, #111111);
	padding: 25px 40px;
	overflow: auto;
}

@media only screen and (max-width: 424px) {
	.app-left {
		width: 0%;
		background: -webkit-gradient(linear, left top, left bottom, from(rgb(208, 130, 94)), to(rgb(179, 93, 54)));
		background: linear-gradient(rgb(208, 130, 94), rgb(179, 93, 54));
		overflow: auto;
		box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
	}
	  
	.app-right {
		width: 100%;
		background: -webkit-gradient(linear, left top, left bottom, from(#2E4158), to(#111111));
		background: linear-gradient(#2E4158, #111111);
		padding: 25px 40px;
		overflow: auto;
	}
}
  
.toggle {
	display: flex;
	justify-content: flex-end;
	margin-bottom: 10%;

	/* WHEN READY FOR SOCIAL FEATURES, DELETE THIS! :) */
	display: none;
}
  
.toggle-item {
	background-color: #4C5D72;
	color: #9DA6B1;
	padding: 10px 25px;
	cursor: pointer;
	font-size: .9em;
	border: none;
	outline: none;
	display: inline-block;
	text-decoration: none;
    box-shadow: 10px 10px rgba(51, 51, 51, 0.418);
}
  
.toggle-item-active {
	background-color: rgb(208, 130, 94);
	color: white;
}
  
.toggle-item:first-child {
	border-top-left-radius: 25px;
	border-bottom-left-radius: 25px;
}

.toggle-item:last-child {
	border-top-right-radius: 25px;
	border-bottom-right-radius: 25px;
}

